<template>
	<div id="store-basic">
		<public-header title="基本信息"></public-header>
		<div class="content" v-if="store">
			<van-cell-group>
				<van-cell title="门店名称" :value="store.title ? store.title : '未设置'" is-link :to="util.getUrl({path: '/plugin/pages/paotui/store/shop/update', query: {type: 'title'}})" />
				<van-cell class="store-logo flex-lr" title="门店Logo" :to="util.getUrl({path: '/plugin/pages/paotui/store/shop/update', query: {type: 'logo'}})">
					<div slot="right-icon" class="flex ">
						<img :src="store.logo" alt="" v-if="store.logo">
						<span v-else>未设置</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell title="门店描述" :value="store.content ? store.content : '未设置'" is-link :to="util.getUrl({path: '/plugin/pages/paotui/store/shop/update', query: {type: 'content'}})" />
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="联系电话" :value="store.telephone ? store.telephone : '未设置'" is-link :to="util.getUrl({path: '/plugin/pages/paotui/store/shop/update', query: {type: 'telephone'}})" />
				<van-cell title="详细地址" :value="store.address ? store.address : '未设置'" is-link :to="util.getUrl({path: '/plugin/pages/paotui/store/shop/update', query: {type: 'address'}})" />
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				store: {},
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					url: 'errander/store/info',
					vue: that,
					data: {
					},
					success: function(result) {
						that.store = result.store;
					}
				});
			}
		},
		created() {

		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-basic .van-cell.store-logo img{
		width: 80px;
		height: 60px;
		border-radius: 3px;
	}
	#store-basic .haodian-tag{
		margin-left: 5px;
		max-width: 58px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
</style>


